
import utils from '../../assets/js/urlConfig.js'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {
setStore, getStore ,getUrlKey
}
from '../../config/mUtils'
export default {
    components: {
        Group, Cell,Toast
    },
    data() {
        return {
            items: [],
            toastTxt: '',
            toastShow: false,
            page:'1',
            addressInfo:'',
            orderNo:''
        }
    },
    created() {
        document.title = ""
    },
    mounted() {
      let selAddress = JSON.parse(getStore('selAddress'))
      console.log(selAddress[0])
      if(selAddress[0]){
        this.addressInfo = selAddress[0].item.address
        this.orderNo = selAddress[0].orderNo
      }



      this.myorder()
    },
    methods:{
      myorder(){
        var datas = {
            "openId":getStore('openId'),
            "page":this.page
        }
        this.$http.post(this.HttpUrl.UrlConfig.myorder,datas)
                      .then(res => {
                        var res = res.data
                          if(res.code=='1'){// 
                              this.items = res.data
                          }else{ // 
                            this.toastShow = true
                            this.toastTxt = res.msg
                          }

                      }).catch(error => {
                          console.log(error)
                      })
      }
    }
}




