import {
  SEL_ADDRESS,
  ADD_CART
}
from './mutations_types.js'
import {
  setStore, getStore
}
from '../config/mUtils.js'

export default {
  // 
  [SEL_ADDRESS](state, {
    'item': item,
    'index': index,
    'orderNo': orderNo
  }) {
    let selAddressTemp = []
    selAddressTemp.push({
      'item': item,
      'index': index,
      'orderNo': orderNo
    })

    state.selAddress = selAddressTemp
      //localStorage
    setStore('selAddress', state.selAddress);
  },
  // 
  [ADD_CART](state, {
    'item': item
  }) {
    let addCartListTemp = []
    addCartListTemp.push({
      'item': item
    })

    state.cartList = addCartListTemp
      //localStorage
    setStore('addCartList', state.cartList);
  },
}



