
import {
setStore, getStore ,getUrlKey
}
from '../../config/mUtils'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

export default {
    components: {
        Group,
        Cell,
        XInput,
        XButton,
        PopupPicker
    },
    data() {
        return {
            total: 180,
            num: 1,
            invoiceTypeList:['','',''],
            invoiceTypeValue:[''],
            goodId:'',
            goodNum:'',
            goodSpecId:'',
            goodSpeName:'',
            goodPrice:'',
            address:'',
            person:'',
            phone:'',
            receiptType:'',
            receiptTitle:'',
            receiptNo:'',
            buyerMessage:'',
        }
    },
    created() {
        document.title = ""
    },
    mounted() {

    },
    methods: {
      buyGoods(){
        let datas = {
            'openId':getStore('openId') ,
            'goodId': this.goodId,
            'goodNum': this.goodNum,
            'goodSpecId': this.goodSpecId,
            'goodSpeName': this.goodSpeName,
            'goodPrice': this.goodPrice,
            'address': this.address,
            'person': this.person,
            'phone': this.phone,
            'receiptType': this.receiptType,
            'receiptTitle': this.receiptTitle,
            'receiptNo': this.receiptNo,
            'buyerMessage': this.buyerMessage,
        }
        this.$http.post(this.HttpUrl.UrlConfig.buyGoods, datas)
            .then(res => {
                var res = res.data
                if (res.code == "1") {

                } else {
                    this.$vux.toast.show({
                        text: res.msg,
                        type: 'text',
                        width: '80%'
                    })
                }
            }).catch(error => {
                console.log(error)
            })
      }
    }
}




