
import Vue from 'vue'
import {
setStore, getStore ,getUrlKey
}
from '../../config/mUtils'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Group from 'vux/src/components/group/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'


import {mapMutations} from 'vuex'

const baseList = [{
url: 'javascript:',
img: 'https://static.vux.li/demo/1.jpg',
title: 'fua'
}, {
url: 'javascript:',
img: 'https://static.vux.li/demo/2.jpg',
title: ''
}, {
url: 'javascript:',
img: 'https://static.vux.li/demo/3.jpg',
title: ''
}]
export default {
directives: {
TransferDom
},
components: {
Swiper,
XDialog,
XButton,
Flexbox,
FlexboxItem,
Group,
XNumber
},
data() {
return {
goodTitle:'',
goodSubTitle:'',
goodPrice:'',
goodAddress:'',
goodDes:'',
goodIcon:'',
goodsDatas:[],
show: true,
dialogShow: false,
myAddress: false,
demo01_list: [],
demo01_index: 0,
total: 0,
numValue:1,
price:0,
}
},
mounted() {
this.getGoods()
},
methods: {
...mapMutations([
'ADD_CART'
]),
getGoods(){
var datas = {
"openId":getStore('openId')
}
// console.log(datas)
this.$http.post(this.HttpUrl.UrlConfig.getGoods,datas)
.then(res => {
var res = res.data
if(res.code=='1'){// 
this.goodTitle = res.data[0].goodTitle
this.goodSubTitle = res.data[0].goodSubTitle
this.goodPrice = res.data[0].goodPrice
this.goodAddress = res.data[0].goodAddress
this.goodDes = res.data[0].goodDes
this.goodIcon = res.data[0].goodIcon
this.goodsDatas = res.data[0].goodSpec


this.$nextTick(()=>{
Vue.set(this.goodsDatas[0],'active',true)
this.price = this.goodsDatas[0].price
this.total = parseInt(this.numValue) * this.price
})







let advImg = res.data[0].advImg.split(',')

console.log(advImg)

let advImgArr = []
advImg.forEach((val)=>{
advImgArr.push({
url: 'javascript:',
img: 'https://static.vux.li/demo/1.jpg',
title: ''
})
})
this.demo01_list = advImgArr
}else{ // 
this.$vux.toast.show({
text: res.msg,
type: 'text',
width: '80%'
})
}

}).catch(error => {
console.log(error)
})
},
demo01_onIndexChange(index) {
this.demo01_index = index
},
// 
addAddress() {
this.dialogShow = true

},
change(val) {
console.log('change', val)
this.total = parseInt(val) * this.price
},
handlerBuyBtn() {
this.show = true
},
handlerCloseBtn(){
console.log(11)
this.show = false
},
selGood(item){
let that = this
console.log(item)
this.$nextTick(()=>{
this.goodsDatas.forEach((item)=>{
Vue.set(item,'active',false)
})
Vue.set(item,'active',true)
this.price = item.price
this.total = parseInt(this.numValue) * this.price
})
this.numValue = 1
},
subCart(){
console.log(this.price)
}
}
}




