
import {
setStore, getStore ,getUrlKey
}
from '../../config/mUtils'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
  components: {
    XInput,
      Group,
      Cell,
      Toast,
      XButton
  },
  data(){
    return{
      toastMsg:'',
      toastShow:false,
      takeCode:'',
      goodName:'',
      goodImg:'',
      goodBatch:'',
      reserveMember:'',
      userName:'',
      date:'',
      phone:'',
      address:'',
    }
  },
  created(){
    document.title=""
  },
  mounted(){
    this.takeGoodInfo()
  },
  methods:{
    takeGoodInfo(){
      let datas = {
          'openId': getStore('openId'),
          'takeCode': getUrlKey('takeCode')
      }
      this.$http.post(this.HttpUrl.UrlConfig.takeGood, datas)
          .then(res => {
              var res = res.data
              if (res.code == "1") {
                this.takeCode = res.data.takeCode
                this.goodName = res.data.goodName
                this.goodImg = res.data.goodImg
                this.reserveMember = res.data.reserveMember
                this.goodBatch = res.data.goodBatch
                this.date = ''+res.data.sendTime
              } else {
                  this.$vux.toast.show({
                      text: res.msg,
                      type: 'text',
                      width: '80%'
                  })
              }
          }).catch(error => {
              console.log(error)
          })
    },
    confirmTakeGood(){
      // let result = ''
      // this.$router.push(
      //   {
      //     path: '/goods-sucess',
      //     query: { result:  result}
      //   })

      var datas = {
          "openId":getStore('openId'),
          "takeCode":this.takeCode,
          "address":this.address,
          "person":this.userName,
          "phone":this.phone,
          "goodId":'',
          "reserveMember":this.reserveMember
      }
      this.$http.post(this.HttpUrl.UrlConfig.confirmTakeGood,datas)
                    .then(res => {
                      var res = res.data
                        if(res.code=='1'){// 
                          let result = res.result
                          let that = this;
                          // let result = ''
                          this.$vux.toast.show({
                              text: '',
                              type: 'text',
                              width: '80%',
                              time:'2000',
                              onHide(){
                                that.$router.push(
                                  {
                                    path: '/goods-sucess',
                                    query: { result:  result}
                                  })
                              }
                          })


                        }else{ // 
                          this.$vux.toast.show({
                              text: res.msg,
                              type: 'text',
                              width: '80%'
                          })
                        }

                    }).catch(error => {
                        console.log(error)
                    })
    }
  }
}




