
import Toast from 'vux/src/components/toast/index.vue'

import {setStore,getStore} from '../config/mUtils.js'

export default {
components: {
Toast
},
data(){
return {
toastTxt: '',
toastShow: false,
}
},
mounted(){
console.log(333)
setStore('openId','123456')
this.isuser()
},
methods:{
isuser(){
this.$http.get(this.HttpUrl.UrlConfig.isuser)
.then(res => {
var res = res.data
if(res.code=='1'){// 
setStore('openId', res.openId)
this.$router.push('/buy-goods');
}else if(res.code=='0'){ // 
this.$router.push('/no-members');
}else{ // 
this.toastShow = true
this.toastTxt = res.msg
}
}).catch(error => {
console.log(error)
})
}

}
}



