
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {
setStore, getStore ,getUrlKey
}
from '../../config/mUtils'
export default {
    components: {
        XHeader,
        XInput, Group,
        XButton,
        PopupPicker,
        Toast
    },
    data() {
        return {
            userName:'',
            phone:'',
            sex:'',
            address:'',
            inviteName:'',
            sexVal:[],
        }
    },
    mounted(){

    },
    methods:{
      submitBtn(){

      },
      register(){
        if(!this.userName){
          this.$vux.toast.show({
              text: '',
              type: 'text',
          })
          return false
        }
        if(!this.phone){
          this.$vux.toast.show({
              text: '',
              type: 'text',
          })
          return false
        }
        if(this.sexVal.length==0){
          this.$vux.toast.show({
              text: '',
              type: 'text',
          })
          return false
        }
        if(!this.address){
          this.$vux.toast.show({
              text: '',
              type: 'text',
          })
          return false
        }
        if(!this.inviteName){
          this.$vux.toast.show({
              text: '',
              type: 'text',
          })
          return false
        }
        var datas = {
          "openId":getStore('openId'),
          "userName":this.userName,
          "phone":this.phone,
          "sex":this.sexVal,
          "address":this.address,
          "inviteName":this.inviteName
        }
        this.$http.post(this.HttpUrl.UrlConfig.register,datas)
                      .then(res => {
                        var res = res.data
                          if(res.code=='1'){
                            this.$router.push(
                              {
                                path: '/members'
                              })
                          }else{ // 
                            this.$vux.toast.show({
                                text: res.msg,
                                type: 'text',
                                width: '80%'
                            })
                          }

                      }).catch(error => {
                          console.log(error)
                      })
      }
    }
}




