

import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default {
    components: {
        Group,
        XInput,
        XButton,
        Toast
    },
    data() {
        return {
          toastTxt: '',
          toastShow: false,
          nums: [{
              num: ''
          }, {
              num: ''
          }, {
              num: ''
          }, {
              num: ''
          }, {
              num: ''
          }, {
              num: ''
          }, {
              num: ''
          }, {
              num: ''
          }, {
              num: ''
          }],
        }
    },
    created(){
      document.title=""
    },
    mounted(){


    },
    methods:{
      inviteCode(){

        this.$router.push(
          {
            path: '/apply-members'
          })






        let takeCode = '',
            flag = true
        this.nums.forEach((val, index) => {
            if (!val.num) {
                this.$vux.toast.text('')
                flag = false
                return false
            }
            takeCode += val.num
        })


        if (flag) {
          let datas = {
            "openId":'123456',
            "inviteCode":takeCode
          }
          console.log(datas)
          this.$http.post(this.HttpUrl.UrlConfig.inviteCode,datas)
                        .then(res => {
                          var res = res.data
                            if(res.code=='1'){// 
                              this.$router.push(
                                {
                                  path: '/apply-members'
                                })
                            }else{ // 
                              this.$vux.toast.show({
                                  text: res.msg,
                                  type: 'text',
                                  width: '80%'
                              })
                            }
                        }).catch(error => {
                            console.log(error)
                        })
        }


      }
    }
}




