
import Toast from 'vux/src/components/toast/index.vue'

import {
setStore, getStore ,getUrlKey
}
from '../../config/mUtils'
import {mapMutations} from 'vuex'
export default {
components: {
Toast
},
data() {
return {
items:[],
i: -1,
orderNo:''
}
},
created(){
document.title= ""

},
mounted(){
this.orderNo = getUrlKey('orderNo')
this.addressList()
},
methods:{
addressList(){
let datas = {
"openId":getStore('openId')
}
this.$http.post(this.HttpUrl.UrlConfig.address,datas)
.then(res => {
var res = res.data
if(res.code=='1'){// 
this.items = res.data
}else{ // 
this.$vux.toast.show({
text: res.msg,
type: 'text',
width: '80%'
})
}
}).catch(error => {
console.log(error)
})
},
...mapMutations([
'SEL_ADDRESS'
]),
imgShowClick(item,index){
console.log(this.orderNo)
console.log(index)
this.i = index
this.SEL_ADDRESS({
'item':item,
'index':index,
'orderNo':this.orderNo
});


let datas = {
"openId":getStore('openId'),
"orderNo":this.orderNo,
"orderNoAddress":item.address
}
this.$http.post(this.HttpUrl.UrlConfig.orderNoAddress,datas)
.then(res => {
var res = res.data
if(res.code=='1'){
}else{ // 
this.$vux.toast.show({
text: res.msg,
type: 'text'
})
}
}).catch(error => {
console.log(error)
})
}
}

}




